form.tabbed-form-with-toggle fieldset legend {
  cursor: pointer;
}

.pat-sortable > :hover {
  cursor: move;
  background: #FFF5CD;
  background: #E7E9ED;
}

form.tabbed-form-with-toggle fieldset:not(:nth-child(1)) legend{
  &::after {
    content: '\e84b';
    font-family: fontello;
    font-size: 16px;
    display: inline-block;
    vertical-align: middle;
    transition: all .3s ease-in;
    transform-origin: center center;
    margin-left: 7px;
  }
  &.expanded{
    &:after{
      transform: rotate(180deg);
    }
  }
}

#plone-contentmenu-factories {
  [class*="contenttype-"]{  
    &:before{
      font-family: fontello;
    }
  }
  .contenttype-imio-smartweb-folder::before {
    content: '\e801';
  }
  .contenttype-imio-smartweb-page::before,
  .contenttype-imio-smartweb-procedure::before,
  .contenttype-collective-pivot-family::before{
    content: '\e80e';
  }
  .contenttype-link::before{
    content: '\e806';
  }
  .contenttype-file::before{
    content: '\e811';
  }
  .contenttype-image::before{
    content: '\e810';
  }
  .contenttype-collection::before{
    content: '\e808';
  }
  .contenttype-imio-smartweb-sectionfiles:before,
  .contenttype-imio-smartweb-sectionlinks:before,
  .contenttype-imio-smartweb-sectiontext:before,
  .contenttype-imio-smartweb-sectiongallery:before,
  .contenttype-imio-smartweb-sectionvideo:before{
    content: '\e834';
  }

}

.pat-sortable{
  position: relative;
  display: block;
    // &:after{
