}

.hidden-section-title {
	opacity: 0.3;
}
.show-hide-title{
  display: inline-block;
  margin-left: 15px;
  vertical-align: middle;
}
.content-section{
  h2{
    display: inline-block;
  }
}

.show-hide-banner{
  display: inline-block;
padding: 15px;
background: #fff;
border: solid 1px #e6e6e6;
}

.header-section{
  background: #f2f2f2;
  padding: 0 1rem;
  line-height: 1.9;
  margin-top: 1rem !important;
  margin-bottom: 1rem !important;
}
